<template>
  <div class="store-picture">
    <div class="main-logo">
      <img :src="logoUrl" alt="">
    </div>
    <div class="store-bg">
      <img :src="`${$cdnImageUrl}/share/shop-default-bg.png`" alt="">
    </div>
    <div class="store-info">
      <div class="title">{{storeName}}</div>
      <div v-if="isPartner" class="share-name">由{{$store.state.userInfo.username}}分享</div>
      <div class="qr-wrapper">
        <img :src="canvasSrc" alt="" class="qr">
        <div class="qr-desc">
          <div class="line"><b>①</b> 保存图片到相册</div>
          <div class="line"><b>②</b> 微信扫一扫浏览</div>
        </div>
      </div>
      <div class="logo-line">
        <img :src="`${$cdnImageUrl}/${isSyBird ? 'new_sybird_logo' : 'zhijia'}.png`" alt="">
        <span class="text">{{isSyBird ? '三翼鸟APP': '海尔智家APP'}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { qrcanvas } from 'qrcanvas'
import { getStoreSign } from '@/http/service/shop'
import { myManageinfo } from '@/http/service/mine'
// import List from '../../../views/mine/address/list.vue'

export default {
  // components: { List },
  props: {},
  data () {
    return {
      storeName: '',
      logoUrl: '',
      canvasSrc: '',
      isPartner: false,
      isSyBird: false
    }
  },
  created () {
    this.isSyBird = this.$util.isSyBird()
    this.getStoreInfo()
    this.getIdentify()
    this.createQRCode()
  },
  mounted () {},
  beforeDestroy () {},
  methods: {
    shareLinkFactory() {
      let url = `${location.origin}/sgmobile/shopDetail`
      const query = this.getCleanQuerys()
      const mid = localStorage.getItem('mId')
      const mdCode = localStorage.getItem('mdCode') || ''
      if (mid) {
        query.shareId = mid
      }

      if (mdCode) {
        query.mdCode = mdCode
      }

      const queryString = this.$util.parseParamsToQuery(query)
      return url + queryString
    },
    createQRCode () {
      // const storeId = this.$route.query.storeId
      // let url = `${config.HOST}/sgmobile/shopDetail?storeId=${storeId}`
      // if (mid) {
      //   url += `&shareId=${mid}`
      // }

      // if (mdCode) {
      //   url += `&mdCode=${mdCode}`
      // }
      const url = this.shareLinkFactory()
      this.$nextTick(() => {
        const canvas1 = qrcanvas({
          data: decodeURIComponent(url), // 分享链接（根据需求来）
          size: 152 // 二维码大小
        })
        this.canvasSrc = canvas1.toDataURL('image/png')
      })
    },

    // ajax
    getIdentify () {
      myManageinfo().then((res) => {
        if (res.success) {
          if (res.data.data.identifyFlag && [22, 21, 31].includes(res.data.data.identifyFlag)) {
            this.isPartner = true
          }
        }
      })
    },
    getStoreInfo() {
      const storeId = localStorage.getItem('baseStoreId') || this.$route.query.storeId
      let longitude = ''
      let latitude = ''
      if (this.location) {
        longitude = this.location.longitude
        latitude = this.location.latitude
      }

      getStoreSign({
        shareId: localStorage.getItem('mId') || '',
        storeId,
        mdCode: localStorage.getItem('mdCode') || '',
        longitude,
        latitude
      }).then(res => {
        if (res.success) {
          this.storeName = res.data.o2oStoreName || res.data.storeName
          if (res.data.logoUrl) {
            this.logoUrl = res.data.logoUrl
          } else {
            if (Number(res.data.synStoreType) !== 9 || this.isSyBird) {
              this.logoUrl = `${this.$cdnImageUrl}/sybird_default_logo.png`;
            } else {
              this.logoUrl = `${this.$cdnImageUrl}/uplus_logo.png`;
            }
          }
        }
      })
    }
  },
  computed: {
    location() {
      return this.$store.getters['getlocationInfo']
    }
  },
  watch: {}
}
</script>
